import Vue from 'vue'
import VueRouter from 'vue-router'
import Cv from '../views/Cv.vue'
import About from '../views/About.vue'
import Education from '../views/Education.vue'
import Experience from '../views/Experience.vue'
import Formation from '../views/Formation.vue'
import Logros from '../views/Logros.vue'
import Skills from '../views/Skills.vue'
import References from '../views/References.vue'
import NotFount from '../views/404.vue'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'cv',
    component: Cv
  },

  {
    path: '/sobre-mi',
    name: 'about',
    component: About
  },
  {
    path: '/educacion',
    name: 'education',
    component: Education
  },
  {
    path: '/experiencia',
    name: 'experience',
    component: Experience
  },
  {
    path: '/formacion-profesional',
    name: 'formation',
    component: Formation
  },
  {
    path: '/logros',
    name: 'logros',
    component: Logros
  },
  {
    path: '/habilidades',
    name: 'skills',
    component: Skills
  },
  {
    path: '/referencias',
    name: 'references',
    component: References
  },
  {
    path: '*',
    component: NotFount
  }
  
]

const router = new VueRouter({
  routes,
})

export default router
